@import url(https://fonts.googleapis.com/css2?family=Bitter&display=swap);
.card-body{
  width: 100% !important;
}
.bookCard {
  -webkit-align-items: center;
          align-items: center;
  margin-top: 2em;
}
.bookCardTitle, .bookCardAuthor {
  height: 1.25em;
  overflow-y: hidden;
}
.bookCardDescription{
  height: 10rem;
  overflow-y: scroll;
}
.imgDiv {
  display: -webkit-flex;
  display: flex;
  height: 200px;
  -webkit-align-items: center;
          align-items: center;
}
.bookCardImg{
  max-width: 150px;
  max-height: 180px;
  margin-top: 0.5em;
}
.viewButton{
  margin-right: 1em;
}
.App {
  text-align: center;
  font-family: "Bitter", serif;
  margin-bottom: 50px;
}
.container-fluid {
  padding-left: 30px !important;
  padding-right: 30px !important;
}


